import React from "react";
import { onFollowingLoad, getUsers, unfollow, follow } from "../redux/NewFriendReducer";
import { connect } from "react-redux";
import { NewUserFriend } from "./NewUserFriend";
import { compose } from "redux";
import { NewFriendSelector } from "../redux/NewFriendSelector";
class NewUserFriendContainerComponent extends React.Component {
    componentDidMount() {
        const { selectedPage, pageSize } = this.props
        this.props.getUsers(selectedPage, pageSize)
    }
    onPageChenged = (pageNumber) => {
        this.props.getUsers(pageNumber, this.props.pageSize)
    }
    render() {
        return (
            <>
                <NewUserFriend isFetching={this.props.isFetching} totalUsersCount={this.props.totalUsersCount} pageSize={this.props.pageSize}
                    onPageChenged={this.onPageChenged} selectedPage={this.props.selectedPage}
                    users={this.props.users} isFollowingLoad={this.props.isFollowingLoad}
                    unfollow={this.props.unfollow} follow={this.props.follow}></NewUserFriend >

            </>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        users: NewFriendSelector.getUsers(state),//state.newUserFriend.users,
        // users: getUsersSuper(state),
        pageSize: NewFriendSelector.getPageSize(state),//state.newUserFriend.pageSize,
        totalUsersCount: NewFriendSelector.getTotalUsersCount(state),//state.newUserFriend.totalUsersCount,
        selectedPage: NewFriendSelector.getSelectedPage(state),//state.newUserFriend.selectedPage,
        isFetching: NewFriendSelector.getIsFetching(state),//state.newUserFriend.isFetching,
        isFollowingLoad: NewFriendSelector.getFollowLoad(state),//state.newUserFriend.followLoad
    }
}
const NewUserFriendContainer = compose(
    connect(mapStateToProps,
        { onFollowingLoad, getUsers, unfollow, follow })
)(NewUserFriendContainerComponent)
export default NewUserFriendContainer