// import { createSelector } from "reselect"
export const NewFriendSelector = {
    getUsers(state) {
        return state.newUserFriend.users
    },
    getPageSize(state) {
        return state.newUserFriend.pageSize
    },
    getTotalUsersCount(state) {
        return state.newUserFriend.totalUsersCount
    },
    getSelectedPage(state) {
        return state.newUserFriend.selectedPage
    },
    getIsFetching(state) {
        return state.newUserFriend.isFetching
    },
    getFollowLoad(state) {
        return state.newUserFriend.followLoad
    },
}
