import React from "react";
import { Pagenation } from "../common/pagenation";
import { User } from "./User";
import { PreloaderBar } from "../common/PreloaderBar";
export const NewUserFriend = (props) => {
  return (
    <div>
      {props.isFetching
        ? <PreloaderBar />
        : props.users.map(user => <User key={user.id} user={user} isFollowingLoad={props.isFollowingLoad} unfollow={props.unfollow} follow={props.follow}></User>)
      }
      <Pagenation onPageChenged={props.onPageChenged} selectedPage={props.selectedPage} totalItemsCount={props.totalUsersCount} pageSize={props.pageSize} portionSize={10} />
    </div >
  );
};
