import React from "react"
import { NavLink } from "react-router-dom";
export const User = ({ user, isFollowingLoad, unfollow, follow }) => {
    let smallPhoto = "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTK11Cw35B_qnaEhLud9e2jBqYfaouUMuWdTdRvV94r7A&s"
    return (
        <div >
            <NavLink to={'/profile/' + user.id}>
                <img style={{ maxWidth: "80px" }} src={user.photos.small != null ?
                    user.photos.small : smallPhoto} alt="" />
                <span style={{ padding: "0px 20px" }}>{user.name}</span>
            </NavLink>
            <button disabled={isFollowingLoad.some(id => id === user.id)} onClick={() => {
                user.followed === true ? unfollow(user.id) : follow(user.id)
            }}>
                {user.followed === true ? "FOLLOW" : "UNFOLLOW"}
            </button>
        </div>
    )
}