import { useState } from "react"

export const Pagenation = ({ onPageChenged, selectedPage, totalItemsCount, pageSize, portionSize }) => {
    const pages = []
    const pagesCount = Math.ceil(totalItemsCount / pageSize)
    const portionCount = Math.ceil(pagesCount / portionSize)
    const [portionNumber, setPortionNumber] = useState(1)
    const startPortionNumber = (portionNumber - 1) * portionSize + 1
    const endPortionNumber = portionNumber * portionSize
    for (let i = 1; i <= pagesCount; i++) { pages.push(i) }
    return (<>
        <div style={{ display: "flex", justifyContent: "center", paddingTop: "50px" }}>
            <button disabled={portionNumber <= 1} onClick={() => setPortionNumber(portionNumber - 1)}>{"< Prevision"}</button>
            {pages
                .filter(p => p >= startPortionNumber && p <= endPortionNumber)
                .map(p => {
                    return <button disabled={selectedPage === p} onClick={(e) => { onPageChenged(p) }} key={p} style={{ fontWeight: "400", margin: "5px" }}>{p}</button>
                })}
            <button disabled={portionCount <= portionNumber} onClick={() => setPortionNumber(portionNumber + 1)}>{"Next >"}</button>
        </div>
    </>)
}